'use client';

import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useUser } from '@clerk/nextjs';
import { ModulePageTemplate } from '../../../components/organisms/ModulePageTemplate';
import { ArrowLeft, Play, Star, Lock } from 'lucide-react';

interface Module {
  id: string;
  name: string;
  icon: string;
  description: string;
  color: string;
  bgColor: string;
  progress: number;
  stars: number;
  isUnlocked: boolean;
}

interface Level {
  id: number;
  name: string;
  description: string;
  isUnlocked: boolean;
  stars: number;
  isCompleted: boolean;
  preview: string;
  difficulty: 'fácil' | 'medio' | 'difícil';
}

export default function ConjuntosPage() {
  const router = useRouter();
  const { user } = useUser();
  
  const [coinCount, setCoinCount] = useState(5);
  
  // Módulo específico de conjuntos
  const conjuntosModule: Module = {
    id: "conjuntos",
    name: "CONJUNTOS",
    icon: "./images/icons/conjuntos.png",
    description: "Aprende a agrupar y clasificar elementos. Mientras más niveles completes y lo logres con 3 estrellas obtendrás más monedas",
    color: "#FF69B4",
    bgColor: "from-pink-200 to-purple-200",
    progress: 0,
    stars: 0,
    isUnlocked: true,
  };

  // Todos los módulos para navegación
  const allModules: Module[] = [
    conjuntosModule,
    {
      id: "numeracion",
      name: "NUMERACIÓN",
      icon: "./images/icons/numeracion.png",
      description: "Aprende números del 1 al 100",
      color: "#4169E1",
      bgColor: "from-blue-200 to-indigo-200",
      progress: 0,
      stars: 0,
      isUnlocked: false,
    },
    {
      id: "suma",
      name: "SUMA",
      icon: "./images/icons/suma.png",
      description: "Domina las sumas básicas",
      color: "#32CD32",
      bgColor: "from-green-200 to-emerald-200",
      progress: 0,
      stars: 0,
      isUnlocked: false,
    },
    {
      id: "resta",
      name: "RESTA",
      icon: "./images/icons/resta.png",
      description: "Aprende a restar correctamente",
      color: "#FF4500",
      bgColor: "from-orange-200 to-red-200",
      progress: 0,
      stars: 0,
      isUnlocked: false,
    },
    {
      id: "multiplicacion",
      name: "MULTIPLICACIÓN",
      icon: "./images/icons/multiplicacion.png",
      description: "Tablas de multiplicar divertidas",
      color: "#8B4513",
      bgColor: "from-yellow-200 to-orange-200",
      progress: 0,
      stars: 0,
      isUnlocked: false,
    },
    {
      id: "division",
      name: "DIVISIÓN",
      icon: "./images/icons/division.png",
      description: "Divide y comparte equitativamente",
      color: "#20B2AA",
      bgColor: "from-cyan-200 to-teal-200",
      progress: 0,
      stars: 0,
      isUnlocked: false,
    },
    {
      id: "geometria",
      name: "GEOMETRÍA",
      icon: "./images/icons/geometria.png",
      description: "Explora formas y figuras",
      color: "#9370DB",
      bgColor: "from-purple-200 to-pink-200",
      progress: 0,
      stars: 0,
      isUnlocked: false,
    },
  ];

  // Niveles específicos para conjuntos con más detalle
  const conjuntosLevels: Level[] = [
    {
      id: 1,
      name: "Elementos Iguales",
      description: "Agrupa elementos que son exactamente iguales",
      isUnlocked: true,
      stars: 0,
      isCompleted: false,
      preview: "🍎🍎 | 🍌🍌",
      difficulty: 'fácil'
    },
    {
      id: 2,
      name: "Elementos Diferentes",
      description: "Identifica elementos que son diferentes",
      isUnlocked: false,
      stars: 0,
      isCompleted: false,
      preview: "🍎🍌 | 🐕🐱",
      difficulty: 'fácil'
    },
    {
      id: 3,
      name: "Más o Menos",
      description: "Compara cantidades entre conjuntos",
      isUnlocked: false,
      stars: 0,
      isCompleted: false,
      preview: "🟦🟦🟦 vs 🟥🟥",
      difficulty: 'medio'
    },
    {
      id: 4,
      name: "Pertenencia",
      description: "¿Este elemento pertenece al conjunto?",
      isUnlocked: false,
      stars: 0,
      isCompleted: false,
      preview: "🍎 ∈ {🍎,🍌,🍇}?",
      difficulty: 'medio'
    },
    {
      id: 5,
      name: "Intersección",
      description: "Encuentra elementos comunes entre conjuntos",
      isUnlocked: false,
      stars: 0,
      isCompleted: false,
      preview: "{🍎,🍌} ∩ {🍌,🍇}",
      difficulty: 'difícil'
    }
  ];

  const handleBack = () => {
    router.push('/dashboard');
  };

  const handleModuleChange = (newModuleId: string) => {
    const newModule = allModules.find(m => m.id === newModuleId);
    if (newModule?.isUnlocked) {
      router.push(`/modules/${newModuleId}`);
    }
  };

  const handleLevelStart = (level: number) => {
    const selectedLevel = conjuntosLevels.find(l => l.id === level);
    if (selectedLevel?.isUnlocked) {
      console.log(`Iniciando nivel ${level} de conjuntos`);
      // Navegar a la página específica del nivel
      router.push(`/modules/conjuntos/level/${level}`);
    }
  };

  const handleVideoPlay = () => {
    console.log("Reproducir video explicativo de conjuntos");
    // Aquí puedes implementar la lógica del video
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-50 via-purple-50 to-pink-100">
      {/* Header personalizado para conjuntos */}
      <div className="bg-gradient-to-r from-pink-500 to-purple-600 text-white p-6">
        <div className="flex items-center gap-4 mb-4">
          <button
            onClick={handleBack}
            className="bg-white/20 hover:bg-white/30 p-2 rounded-full transition-all duration-200"
          >
            <ArrowLeft className="w-6 h-6" />
          </button>
          <div className="flex items-center gap-3">
            <img 
              src={conjuntosModule.icon} 
              alt="Conjuntos" 
              className="w-12 h-12"
            />
            <div>
              <h1 className="text-2xl font-bold">{conjuntosModule.name}</h1>
              <p className="text-pink-100">¡Aprende a agrupar elementos!</p>
            </div>
          </div>
        </div>
        
        {/* Video intro específico para conjuntos */}
        <div className="bg-white/10 rounded-xl p-4 mb-4">
          <div className="flex items-center justify-between">
            <div>
              <h3 className="font-semibold mb-1">Video Explicativo</h3>
              <p className="text-sm text-pink-100">¿Qué son los conjuntos?</p>
            </div>
            <button
              onClick={handleVideoPlay}
              className="bg-pink-400 hover:bg-pink-500 p-3 rounded-full transition-all duration-200"
            >
              <Play className="w-6 h-6" />
            </button>
          </div>
        </div>
      </div>

      {/* Contenido principal */}
      <div className="p-6">
        {/* Descripción específica de conjuntos */}
        <div className="bg-white rounded-2xl p-6 shadow-lg mb-6">
          <h2 className="text-xl font-bold text-gray-800 mb-3">Sobre los Conjuntos</h2>
          <p className="text-gray-600 mb-4">
            Los conjuntos son grupos de elementos que tienen algo en común. 
            ¡Es como organizar tus juguetes por colores o tipos!
          </p>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="text-center p-3 bg-pink-50 rounded-xl">
              <div className="text-2xl mb-2">🍎🍎🍎</div>
              <p className="text-sm font-medium">Manzanas</p>
            </div>
            <div className="text-center p-3 bg-purple-50 rounded-xl">
              <div className="text-2xl mb-2">🐕🐱🐰</div>
              <p className="text-sm font-medium">Animales</p>
            </div>
            <div className="text-center p-3 bg-blue-50 rounded-xl">
              <div className="text-2xl mb-2">🟦🟪🟢</div>
              <p className="text-sm font-medium">Colores</p>
            </div>
            <div className="text-center p-3 bg-yellow-50 rounded-xl">
              <div className="text-2xl mb-2">123</div>
              <p className="text-sm font-medium">Números</p>
            </div>
          </div>
        </div>

        {/* Niveles con diseño específico para conjuntos */}
        <div className="bg-white rounded-2xl p-6 shadow-lg">
          <h2 className="text-xl font-bold text-gray-800 mb-6">Niveles del Módulo</h2>
          <div className="grid gap-4">
            {conjuntosLevels.map((level) => (
              <div
                key={level.id}
                className={`relative border-2 rounded-xl p-4 transition-all duration-300 ${
                  level.isUnlocked
                    ? 'border-pink-200 bg-gradient-to-r from-pink-50 to-purple-50 hover:shadow-lg cursor-pointer'
                    : 'border-gray-200 bg-gray-50'
                }`}
                onClick={() => level.isUnlocked && handleLevelStart(level.id)}
              >
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-4">
                    <div className={`w-12 h-12 rounded-full flex items-center justify-center font-bold text-lg ${
                      level.isUnlocked ? 'bg-pink-500 text-white' : 'bg-gray-300 text-gray-500'
                    }`}>
                      {level.isUnlocked ? level.id : <Lock className="w-6 h-6" />}
                    </div>
                    <div>
                      <h3 className={`font-bold ${level.isUnlocked ? 'text-gray-800' : 'text-gray-400'}`}>
                        {level.name}
                      </h3>
                      <p className={`text-sm ${level.isUnlocked ? 'text-gray-600' : 'text-gray-400'}`}>
                        {level.description}
                      </p>
                      <div className="flex items-center gap-2 mt-1">
                        <span className={`text-xs px-2 py-1 rounded-full ${
                          level.difficulty === 'fácil' ? 'bg-green-100 text-green-700' :
                          level.difficulty === 'medio' ? 'bg-yellow-100 text-yellow-700' :
                          'bg-red-100 text-red-700'
                        }`}>
                          {level.difficulty}
                        </span>
                        <span className="text-sm font-mono text-purple-600">
                          {level.preview}
                        </span>
                      </div>
                    </div>
                  </div>
                  
                  <div className="flex items-center gap-2">
                    {/* Estrellas */}
                    <div className="flex gap-1">
                      {[1, 2, 3].map((star) => (
                        <Star
                          key={star}
                          className={`w-5 h-5 ${
                            star <= level.stars
                              ? 'text-yellow-400 fill-current'
                              : 'text-gray-300'
                          }`}
                        />
                      ))}
                    </div>
                    
                    {level.isUnlocked && (
                      <div className="w-8 h-8 bg-pink-500 rounded-full flex items-center justify-center">
                        <Play className="w-4 h-4 text-white" />
                      </div>
                    )}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Consejos específicos para conjuntos */}
        <div className="mt-6 bg-gradient-to-r from-purple-100 to-pink-100 rounded-2xl p-6">
          <h3 className="text-lg font-bold text-purple-800 mb-3">💡 Consejos para Conjuntos</h3>
          <div className="grid md:grid-cols-2 gap-4 text-sm text-purple-700">
            <div className="flex items-start gap-2">
              <span>🔍</span>
              <div>
                <strong>Observa bien:</strong> Mira las características de los elementos antes de agruparlos.
              </div>
            </div>
            <div className="flex items-start gap-2">
              <span>🎯</span>
              <div>
                <strong>Busca patrones:</strong> Los elementos del mismo conjunto tienen algo en común.
              </div>
            </div>
            <div className="flex items-start gap-2">
              <span>💪</span>
              <div>
                <strong>Practica:</strong> Mientras más practiques, mejor serás organizando elementos.
              </div>
            </div>
            <div className="flex items-start gap-2">
              <span>⭐</span>
              <div>
                <strong>Ve despacio:</strong> No hay prisa, piensa bien antes de responder.
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}